<!--  -->
<template>
  <div>
    <Breadcrumb :value="common"></Breadcrumb>
    <el-card class="box-card">
      <el-table :data="tableData" stripe style="width: 100%" border>
        <el-table-column type="index" label="#" width="80"></el-table-column>
        <el-table-column prop="order_number" label="" width="180"></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="order_pay" label="">
          <template slot-scope="scope">
            <el-tag type="danger" v-if="scope.row.order_pay==='0'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">{{scope.row.update_time | moment}}</template>
        </el-table-column>
        <el-table-column prop="address" label="">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="openSide"></el-button>
            <!--  -->
            <el-button type="success" icon="el-icon-s-promotion" size="mini" @click="openOrders"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- // -->
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="orderfrom.pagenum"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="orderfrom.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="sideVisible" width="50%" :before-close="addressClose">
      <el-form ref="OrderSideFormRef" label-width="110px" :model="addressForm" :rules="addressFormRules">
        <!--  -->
        <el-form-item label="">
          <el-cascader
            style="width:20%"
            clearable
            v-model="addressForm.selectedKeys"
            :options="options"
            :props="{ 
              expandTrigger: 'hover',
              value:'value',
              label:'label',
                children:'children'
               }"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
        <el-form-item
          label=":"
          prop="side"
        >
          <el-input  v-model="addressForm.side" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" ></el-button>
          <el-button @click="emptyAddress"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="ordersVisible" width="50%">
      <el-timeline :reverse="reverse">
        <el-timeline-item
          v-for="(activity, index) in activities"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>
<script>
import { ordersList_api, ordersPany_api } from '@/api'
import cityData from './citydata'
export default {
  data() {
    return {
      common: ['', ''], //
      orderfrom: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      //   
      tableData: [],
      total: 0,
      //   
    //   selectedKeys: [],
      sideVisible: false,
      options: cityData,
      addressForm:{
          selectedKeys:[],
          side:''
      },
    //   
      addressFormRules:{
          side: [{ required: true, message: '' }]
      },
      //   
      ordersVisible: false,
      reverse: true,
      activities: []
    }
  },
  created() {
    this.getordersList()
  },
  methods: {
    async getordersList() {
      const { data: res } = await ordersList_api(this.orderfrom)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.tableData = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(val) {
      this.orderfrom.pagesize = val
      this.getordersList()
    },
    handleCurrentChange(val) {
      this.orderfrom.pagenum = val
      this.getordersList()
    },
    // 
    openSide() {
      this.sideVisible = true
    },
    // 
    handleChange(val) {
        // console.log(val);
        this.addressForm.side=val.join('')
    },
    // 
    emptyAddress(){
       this.addressForm.selectedKeys=[]
        this.$refs.OrderSideFormRef.resetFields()
    },
    // 
    addressClose(){
        this.addressForm.selectedKeys=[]
        this.$refs.OrderSideFormRef.resetFields()
        this.sideVisible = false
    },
    // 
    async openOrders() {
      const { data: res } = await ordersPany_api()
      // console.log(res);
      if (res.meta.status != 200) return this.$message.error('')
      this.$message.success('')
      console.log(res.data)
      this.activities = res.data
      this.ordersVisible = true
    }
  }
}
</script>
<style lang='less' scoped>
.el-cascader {
  width: 100% !important;
}
</style>